/* ============================
   CONTAINER & LAYOUT
   ============================ */
.skills-main-container {
  position: relative;
  overflow-x: hidden; /* Prevents horizontal scrollbar caused by animations */
  min-height: 80vh; /* ENSURES container always takes space */
  padding: 50px 0;
}

/* ============================
     ANIMATED BACKGROUND ELEMENTS
     ============================ */
/* Tech Lines */
.tech-line {
display: None;
}

.tech-line-1 {
  top: 20%;
  left: 0;
  width: 100%;
  height: 1px;
}
.tech-line-2 {
  top: 50%;
  left: 0;
  width: 100%;
  height: 1px;
}
.tech-line-3 {
  top: 80%;
  left: 0;
  width: 100%;
  height: 1px;
}

/* Neural network nodes */
.neural-node {
  position: absolute;
  width: 8px;
  height: 8px;
  background: radial-gradient(
    circle,
    rgba(124, 209, 247, 1),
    rgba(124, 209, 247, 0.3)
  );
  border-radius: 50%;
  box-shadow: 0 0 20px rgba(124, 209, 247, 0.8);
  animation: nodePulse 3s ease-in-out infinite;
  pointer-events: none;
  z-index: 1;
}

/* Simplified animation for better performance */
@keyframes nodePulse {
  0%,
  100% {
    transform: scale(1);
    opacity: 1;
  }
  50% {
    transform: scale(1.5);
    opacity: 0.7;
  }
}

.neural-node-1 {
  top: 15%;
  left: 15%;
}
.neural-node-2 {
  top: 25%;
  right: 20%;
  background: #ff7f50;
}
.neural-node-3 {
  bottom: 20%;
  left: 25%;
  background: #a7f432;
}
.neural-node-4 {
  bottom: 30%;
  right: 15%;
}

/* ============================
     HEADER STYLING
     ============================ */
.skills-header-div {
  margin-bottom: 30px;
}

.skills-header,
.skills-heading {
  font-size: 48px;
  line-height: 1.3;
  font-weight: 700;
  text-align: center;
  font-family: "Google Sans Medium", sans-serif;
  margin: 0 auto;
  position: relative;
  padding: 20px 40px;
}

.skills-header span {
  display: inline-block;
  background: linear-gradient(45deg, #7cd1f7, #2962ff, #ff7f50);
  background-size: 200% 200%;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  animation: holoShift 5s ease infinite; /* Sped up slightly */
  filter: drop-shadow(0 0 5px rgba(124, 209, 247, 0.3));
}

@keyframes holoShift {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}

/* ============================
     SECTION CONTENT
     ============================ */
.skills-section-wrapper {
  position: relative;
  z-index: 2;
  width: 100%;
}

/* 
     NOTE: Assuming SkillSection returns a div with class 'skills-main-div'.
     We define it here to ensure layout works.
  */
.skills-main-div {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 40px;
  padding: 0 40px;
  max-width: 1400px;
  margin: 0 auto;
}

/* Text Section */
.skills-text-div {
  flex: 1;
  display: flex;
  flex-direction: column;
}

.skills-text {
  font-size: 20px;
  line-height: 1.6;
}

.skills-text-subtitle {
  font-size: 18px;
  margin-top: 15px;
  opacity: 0.9;
}

/* Image Section */
.skills-image-div {
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  /* Removed perspective to reduce render cost */
}

.skills-image-div > * {
  max-width: 100%;
  height: auto;
  /* Optimized Levitation */
  animation: levitate 4s ease-in-out infinite;
  filter: drop-shadow(0 10px 20px rgba(0, 0, 0, 0.15));
}

@keyframes levitate {
  0%,
  100% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(-15px);
  }
}

/* ============================
     MOBILE OPTIMIZATION (CRITICAL FIXES)
     ============================ */
@media (max-width: 768px) {
  .skills-main-container {
    padding: 30px 0;
    min-height: auto; /* Let content dictate height */
  }

  /* 1. HIDE Heavy Background Elements on Mobile */
  .tech-line,
  .neural-node,
  .hex-particle,
  .liquid-drop,
  .rotate-ring,
  .energy-core {
    display: none !important;
  }

  /* 2. Fix Header Size */
  .skills-heading,
  .skills-header {
    font-size: 32px;
    padding: 10px 15px;
  }

  /* 3. Stack Content Vertically */
  .skills-main-div {
    flex-direction: column;
    padding: 0 20px;
    gap: 30px;
  }

  /* 4. Fix Image Order & Size */
  .skills-image-div {
    order: -1; /* Image on top */
    width: 100%;
    margin-top: 10px;
  }

  .skills-image-div > * {
    max-width: 80%; /* Don't let image overflow */
    margin: 0 auto;
    /* DISABLE HEAVY FILTERS ON MOBILE */
    filter: none !important;
    animation: levitate 4s ease-in-out infinite; /* Keep simple animation */
  }

  /* 5. Fix Text Styling */
  .skills-text-div {
    width: 100%;
    text-align: center;
    padding: 0;
  }

  .skills-text {
    font-size: 16px;
    line-height: 1.5;
  }

  .skills-text-subtitle {
    font-size: 15px;
    margin-top: 10px;
  }

  /* 6. Fix for React Reveal (Fade) */
  /* This ensures content is visible even if the JS animation fails to trigger */
  .react-reveal {
    opacity: 1 !important;
    animation: none !important;
    visibility: visible !important;
  }
}

@media (max-width: 480px) {
  .skills-heading {
    font-size: 28px;
  }
  .skills-main-div {
    padding: 0 15px;
  }
  .skills-image-div > * {
    max-width: 90%;
  }
}
