    circle at center,
    rgba(255, 255, 255, 0.1) 0%,
    transparent 70%
  );
  transform: scale(0);
  transition: transform 0.3s ease;
}

.home-social-media-div a:hover::before {
  transform: scale(1.5);
}

/* Enhance button interaction */
