/* =========================================================
   üíé TOP-NOTCH CRYSTAL PRISM (With Sheen & Glow)
   ========================================================= */

   .skill-item, .achievement-item {
    position: relative;
    display: flex;
    align-items: center;
    gap: 1.2rem;
    padding: 1.2rem 1.6rem; /* Slightly more breathing room */
    
    /* 1. GLASS BASE */
    background: linear-gradient(
      160deg, 
      rgba(255, 255, 255, 0.05) 0%, 
      rgba(255, 255, 255, 0.01) 100%
    );
    backdrop-filter: blur(12px);
    -webkit-backdrop-filter: blur(12px);
    border-radius: 18px; /* Smooth corners */
    overflow: hidden;    /* Crucial for the sheen effect */
    
    /* 2. SKEUOMORPHIC EDGES (Crisp Light & Shadow) */
    border: 1px solid rgba(255, 255, 255, 0.05);
    border-top: 1px solid rgba(255, 255, 255, 0.3); /* Catching top light */
    border-left: 1px solid rgba(255, 255, 255, 0.15); /* Catching side light */
    border-bottom: 1px solid rgba(0, 0, 0, 0.4);     /* Deep shadow */
    
    box-shadow: 
      0 10px 40px -10px rgba(0,0,0,0.5), /* Soft ambient shadow */
      inset 0 0 0 1px rgba(255, 255, 255, 0.02); /* Inner faint rim */
      
    transition: all 0.4s cubic-bezier(0.175, 0.885, 0.32, 1.275); /* Bouncy spring feel */
    cursor: default;
  }
  
  /* 3. THE "SHEEN" SWEEP ANIMATION */
  .skill-item::before, .achievement-item::before {
    content: "";
    position: absolute;
    top: 0;
    left: -100%; /* Start off-screen */
    width: 100%;
    height: 100%;
    
    /* The Beam of Light */
    background: linear-gradient(
      90deg, 
      transparent, 
      rgba(255, 255, 255, 0.1) 30%, 
      transparent
    );
    transform: skewX(-25deg); /* Tilt it */
    transition: 0.5s;
    pointer-events: none;
  }
  
  /* HOVER STATE: Lift & Sweep */
  .skill-item:hover, .achievement-item:hover {
    transform: translateY(-5px) scale(1.02);
    background: linear-gradient(
      160deg, 
      rgba(255, 255, 255, 0.1) 0%, 
      rgba(255, 255, 255, 0.03) 100%
    );
    border-top-color: rgba(255, 255, 255, 0.6); /* Brighter edge */
    box-shadow: 
      0 20px 50px -10px rgba(0, 0, 0, 0.6), /* Deeper shadow */
      0 0 15px rgba(255, 255, 255, 0.1);    /* Outer glow */
  }
  
  /* Trigger the sheen on hover */

  
  /* 4. ICON GLOW (Neumorphic Depth) */
  .skill-icon, .achievement-icon {
    font-size: 2.2rem;
    /* Make the icon float */
    filter: drop-shadow(0 5px 15px rgba(0, 0, 0, 0.3));
    transition: transform 0.3s ease;
  }
  
  .skill-item:hover .skill-icon, 
  .achievement-item:hover .achievement-icon {
    transform: scale(1.1) rotate(5deg); /* Subtle playful movement */
    filter: drop-shadow(0 0 20px var(--accent-color)); /* Glow with theme color */
  }
  
  /* 5. TEXT REFINEMENT */
  .skill-item span, .achievement-content h3 {
    font-family: "Google Sans Medium";
    text-shadow: 0 2px 4px rgba(0,0,0,0.3); /* Deepen text contrast */
  }
  
  .achievement-content p {
    opacity: 0.8;
    font-size: 0.9em;
  }




@keyframes iconFloat {
