/* ============================================
   üîÆ ROTATING AURA AROUND IMAGE (Subtle)
   ============================================ */

.greeting-image-div::before {
  content: "";
  position: absolute;
  inset: -15%;
  background: radial-gradient(
    circle,
    rgba(99, 102, 241, 0.15) 0%,
    /* Reduced from 0.3 */ rgba(118, 75, 162, 0.1) 40%,
    /* Reduced from 0.2 */ transparent 70%
  );
  border-radius: 50%;
  filter: blur(50px);
  z-index: -1;
  animation: auraPulse 6s ease-in-out infinite; /* Slower: 6s instead of 4s */
}

.greeting-image-div::after {
  content: "";
  position: absolute;
  inset: -20%;
  background: conic-gradient(
    from 0deg,
    transparent 0deg,
    rgba(99, 102, 241, 0.2) 90deg,
    /* Reduced from 0.4 */ transparent 180deg,
    rgba(236, 72, 153, 0.2) 270deg,
    /* Reduced from 0.4 */ transparent 360deg
  );
  border-radius: 50%;
  filter: blur(30px);
  z-index: -2;
  animation: ringRotate 15s linear infinite; /* Slower: 15s instead of 10s */
  opacity: 0.4; /* Reduced from 0.7 */
}

@keyframes auraPulse {
  0%,
  100% {
    opacity: 0.5; /* Reduced from 0.7 */
    transform: scale(1);
  }
  50% {
    opacity: 0.8; /* Reduced from 1 */
    transform: scale(1.02); /* Less scaling: 1.02 instead of 1.05 */
  }
}

@keyframes ringRotate {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(180deg);
  }
}

/* adding for 3d robot head 
