/* ============================================
   üëÅÔ∏è PREMIUM 3D EYE & COUNTER CSS
   ============================================ */

/* 1. Wrapper to keep it INLINE with text */
/* ============================================
   VISITOR COUNTER - SIMPLIFIED
   ============================================ */
/* ‚úÖ SKEUOMORPHIC GLASS CAPSULE */
.premium-counter-wrapper {
  display: inline-flex;
  align-items: center;
  gap: 10px;
  padding: 6px 16px;
  margin-left: 12px;
  border-radius: 50px;
  vertical-align: middle;
  
  /* 1. THE GLASS/LENS MATERIAL */
  background: rgba(0, 0, 0, 0.2); /* Darker tint for contrast */
  backdrop-filter: blur(12px);
  -webkit-backdrop-filter: blur(12px);

  /* 2. SKEUOMORPHIC DEPTH (Recessed Look) */
  /* Top inner shadow = shadow cast by the bezel */
  /* Bottom light border = light catching the bottom edge */
  box-shadow: 
    inset 0 2px 6px rgba(0, 0, 0, 0.5), /* Deep inner shadow at top */
    inset 0 -1px 0 rgba(255, 255, 255, 0.1), /* Subtle light lip at bottom */
    0 4px 15px rgba(0, 0, 0, 0.1); /* Soft outer drop shadow */

  /* 3. REFLECTIVE RIM */
  border: 1px solid rgba(255, 255, 255, 0.05);
  border-bottom: 1px solid rgba(255, 255, 255, 0.02);
  
  transition: all 0.3s cubic-bezier(0.25, 0.8, 0.25, 1);
  cursor: default;
}

/* Hover Effect: The lens lights up slightly */
.premium-counter-wrapper:hover {
  background: rgba(0, 0, 0, 0.3);
  box-shadow: 
    inset 0 2px 6px rgba(0, 0, 0, 0.6),
    inset 0 -1px 0 rgba(255, 255, 255, 0.2), /* Brighter lip */
    0 8px 20px rgba(0, 0, 0, 0.2);
  transform: translateY(-1px);
}

/* 4. THE NUMBER (OLED Display Look) */
.counter-number {
  font-family: "Google Sans Medium", monospace; /* Monospace feels more 'counter' like */
  font-size: 1.1em;
  font-weight: 600;
  
  /* Gradient Metal Text */
  background: linear-gradient(180deg, #ffffff 0%, #d0d0d0 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  
  /* Digital Glow */
  text-shadow: 0 0 15px rgba(255, 255, 255, 0.3);
